@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Main-Body__Content>.pagination {
    position: fixed;
    bottom: 5%;
    left: 50%;
    right: 50%;
    align-items: center;
}

.Main-Body__Content>.pagination>.page-item {
    box-shadow: 0 0 12px 0 var(--shadow);
}

.Main-Body__Content>.pagination>.page-item>.page-link {
    margin: 0;
}

.Main-Body__Content>.pagination>.page-item.disabled>.page-link {
    background-color: var(--primary);
    color: var(--white);
}
/********************************* */
/** COMMIT LIST                    *
/********************************* */
.collapsing {
    background-color: var(--white);
}

.collapse,
.collapse.show {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.page-link {
    color: var(--primary);
}

.page-link:hover {
    color: var(--primary);
    font-weight: bold;
}

/********************************* */
/** COMMIT CARD                    *
/********************************* */
.Commit-List>.Commit-Card {
    text-align: left;
    font-size: 14px;
    padding: .5rem 1rem;
    transition: .2s;
}

.Commit-List>.Commit-Card:hover {
    background-color: var(--white);
}

.Commit-List>.Commit-Card:hover .Commit-Footer>p {
    color: var(--secondary);
}

.Commit-History {
    padding: .5rem;
}

.Commit-History button.btn {
    border: 1px solid var(--primary);
}

.Commit-List>.Commit-Card:not(:last-child) {
    border-bottom: 1px solid var(--tertiary);
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Body {
    display: flex;
    align-items: center;
    padding-right: 1rem;
    margin-bottom: .5rem;
}

.Commit-List>.Commit-Card>.Commit-Content {
    display: flex;
    flex-direction: column;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Body .username {
    color: var(--primary);
    font-weight: 600;
    margin-right: .5rem;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Body p {
    margin: 0;
}

.Commit-List>.Commit-Card>.Commit-Header {
    text-align: left;
    font-weight: bold;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Body>.Commit-Body__Left {
    margin-right: 1rem;
}

.Commit-List>.Commit-Card img {
    border-radius: 100%;
    width: 2rem;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Footer {
    margin-top: auto;
    font-size: small;
    text-align: start;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Body p,
.Commit-List>.Commit-Card>.Commit-Content>.Commit-Footer {
    font-style: italic;
}

.Commit-List>.Commit-Card>.Commit-Content>.Commit-Footer>p {
    color: var(--shadow);
    margin-bottom: .5rem;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (min-width: 600px) {

    .Commit-List>.Commit-Card {
        padding: 1rem 3rem;
    }

    .Commit-List>.Commit-Card>.Commit-Content>.Commit-Body {
        font-size: 16px;
    }

    .Commit-List>.Commit-Card>.Commit-Content>.Commit-Body>.Commit-Body__Left>img {
        width: 3rem;
    }

    .Commit-List>.Commit-Card>.Commit-Content>.Commit-Footer {
        font-size: 14px;
        text-align: end;
        margin-top: auto;
    }
}

@media only screen and (min-width: 768px) {

    .Commit-List>.Commit-Card>.Commit-Content {
        flex-direction: row;
        justify-content: space-between;
    }
}
.Loader-Wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.Loader {
    color: var(--primary) !important;
    height: 4rem;
    width: 4rem;
}
hr {
    border-top: 1px solid var(--tertiary);
    margin-bottom: 0;
}

.Repo-Card__Commit-Toggle .btn-link,
.Repo-Card__Commit-Toggle svg {
    color: var(--primary);
}

.Repo-Card__Commit-Toggle svg {
    margin-right: 8px;
    margin-top: -4px;
}
/********************************* */
/** REPO LIST                      *
/********************************* */
.Repo-List {
    width: 80%;
    margin: auto;
}

/********************************* */
/** REPO CARD                      *
/********************************* */
.Repo-List,
.Repo-Card,
.Repo-Card__Content,
.Repo-Card__Footer,
.Repo-Card__Icons,
.Repo-Card__Stars,
.Repo-Card__Forks,
svg {
    transition: .2s;
}

.Repo-List>.Repo-Card {
    display: flex;
    flex-direction: column;
    margin-bottom: 2.5rem;
    margin-right: auto;
    margin-left: auto;
    background-color: var(--white);
    border-radius: 8px;
    box-shadow: 0 0 8px 0 var(--shadow);
    max-width: 840px;
}

.Repo-List>.Repo-Card>.Repo-Card__Content {
    padding: 1rem 1rem 0 1rem;
    text-align: left;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header {
    display: flex;
    justify-content: space-between;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Title {
    text-transform: uppercase;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons {
    display: flex;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons>.Repo-Card__Stars,
.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons>.Repo-Card__Forks,
.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header {
    display: flex;
    justify-content: space-between;
}

.Repo-Card__Icons svg,
.Repo-Card__Icons p {
    color: var(--tertiary);
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons svg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    margin-right: .5rem;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons>.Repo-Card__Stars {
    margin-right: 1rem;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons>.Repo-Card__Stars svg {
    margin-top: 4px;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Header>.Repo-Card__Icons>.Repo-Card__Forks svg {
    margin-top: 3px;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Footer {
    font-size: small;
    font-weight: 300;
}

.Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Footer p {
    margin-bottom: 0;
}

.Repo-List>.Repo-Card>.Repo-Card__Commit-Toggle button.btn {
    margin-bottom: 1rem;
}

/********************************* */
/** REPO CARD :hover               *
/********************************* */

.Repo-List>.Repo-Card>.Repo-Card__Commit-Toggle button:hover,
.Repo-List>.Repo-Card>.Repo-Card__Commit-Toggle button:focus,
.Repo-List>.Repo-Card>.Repo-Card__Commit-Toggle button:active {
    text-decoration: none;
    background-color: var(--primary);
    color: var(--white);
}

.Repo-Card__Commit-Toggle button.btn:hover svg.bi-clock-history,
.Repo-Card__Commit-Toggle button.btn:active svg.bi-clock-history,
.Repo-Card__Commit-Toggle button.btn:focus svg.bi-clock-history {
    fill: var(--white);
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (min-width: 600px) {

    .Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Footer {
        display: flex;
    }

    .Repo-List>.Repo-Card>.Repo-Card__Content>.Repo-Card__Footer>p:first-child {
        margin-right: 1.5rem;
    }

    .Repo-List>.Repo-Card>.Repo-Card__Content {
        padding: 1rem 3rem 0 3rem;
    }
}
.No-Data {
    width: 80%;
    margin-right: auto;
    margin-left: auto;
}
.Search-Bar {
    width: 80%;
    margin: 0 auto 2rem auto;
    max-width: 840px;
}

#filter {
    outline: none;
}
.Main-Header {
    background-color: var(--primary);
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    letter-spacing: 1px;
    box-shadow: 0 0 10px 0 var(--shadow);
    z-index: 9999;
    padding: 1rem;
}

.Brand {
    display: none;
    margin-right: auto;
    margin-bottom: 0;
    left: 5%;
}

.Brand-Title,
.organization {
    color: var(--white);
}

.Brand>.Brand-Title {
    font-weight: 600;
    letter-spacing: -1px;
    font-size: 20px;
    margin-bottom: 0;
}

.Brand>.Brand-Title.device-xs {
    font-size: 14px;
}

.Brand>.Brand-Title.device-s {
    display: none;
}

.Brand svg {
    fill: var(--white);
    margin-top: auto;
    margin-bottom: auto;
    margin-right: .5rem;
}

h2.organization {
    text-transform: uppercase;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 0;
    text-align: center;
    overflow-wrap: break-word;
}

.Main-Header img {
    display: none;
    height: 36px;
    position: absolute;
    border-radius: 100%;
    object-fit: cover;
}

.Main-Header__Center {
    position: relative;
    display: flex;
    margin-right: auto;
    margin-left: 0;
}

.Organization-Form {
    flex-flow: nowrap;

}

.Organization-Form>.input-group input,
.Organization-Form button {
    height: 1.75rem;
}

.Organization-Form>.input-group>input.form-control {
    width: 60%;
}

.Organization-Form button {
    background-color: var(--tertiary);
    color: var(--white);
    border-color: var(--tertiary);
    padding-top: 0;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (min-width: 600px) {

    .Main-Header img {
        display: block;
    }

    .Main-Header__Center>.organization {
        margin-left: 3rem;
    }

    .Organization-Form {
        margin-left: auto;
        right: 5%;
    }
}

@media only screen and (min-width: 768px) {

    .Brand,
    .Organization-Form {
        display: flex;
        flex: 1 1;
    }

    .Brand {
        justify-content: center;
    }

    .Organization-Form {
        justify-content: flex-end;
    }

    .Brand>.Brand-Title {
        margin-right: auto;
    }

    .Organization-Form>input {
        margin-left: auto;
    }

    .Main-Header img {
        display: block;
        left: -3rem;
    }

    .Main-Header__Center>.organization {
        margin-left: 0;
    }

    .Brand>.Brand-Title.device-s {
        display: block;
    }

    .Organization-Form>.input-group>input.form-control {
        width: 70%;
        transition: .2s;
    }
}
.scrollTop {
    position: fixed;
    width: 100%;
    bottom: 20px;
    height: 20px;
    right: -94%;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
}

.scrollTop:hover {
    opacity: 1;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 0.5;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 0.5;
    }
}
:root {
  --primary: #E50A13;
  --secondary: #222222;
  --tertiary: #949494;
  --bg-primary: #eeeeee;
  --bg-secondary: #333333;
  --white: #fff;
  --shadow: #777;
}

body {
  background-color: #eeeeee;
  background-color: var(--bg-primary);
}

p,
svg {
  color: #222222;
  color: var(--secondary);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #E50A13;
  color: var(--primary);
  font-family: 'Lato', sans-serif;
  font-weight: bold;
}

ul {
  list-style-type: none;
  padding: 0;
}

input {
  outline: none;
}

input::-webkit-input-placeholder {
  font-style: italic;
}

input:-ms-input-placeholder {
  font-style: italic;
}

input::-ms-input-placeholder {
  font-style: italic;
}

input::placeholder {
  font-style: italic;
}

.App {
  text-align: center;
  height: 100vh;
}

.Main-Body {
  display: flex;
}

.Main-Body__Content {
  margin-top: 12vh;
  margin-bottom: 10ch;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
}

.error {
  font-size: 1rem;
  font-style: italic;
  color: #222222;
  color: var(--secondary);
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  max-width: 500px;
}

.error.repo {
  margin-top: 14vh;
}
